.page {
  font-family: Poppins, sans-serif;
  color: white;
  background-color: $menu-color;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
    &:has(.page__menu:target) {
      overflow: hidden;
      }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      // overflow: hidden;
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

}
